import AppBar from '@material-ui/core/AppBar';
import ReactMarkdown from 'react-markdown';

class Skills extends React.Component {
  getSkillsByDomain = (domain, strapiUrl) => {
    const domainSkills = this.props.skills.filter(skill => {
      return skill.domaine._id == domain
    });

    return <div>
      {domainSkills.map(skill => (
        <div key={skill._id}>
          <h3>{skill.Titre}</h3>
          <img src={strapiUrl + skill.Logo.url} alt={hp.Titre} />
        </div>
      ))}
    </div>
  };

  render() {
    const { classes, hp, lang, domains, strapiUrl } = this.props;

    return (
      <div id="section-skills">
        <AppBar position="static" className={classes.appbar}>
          {hp['Titre compétences']}
        </AppBar>
        <ReactMarkdown source={hp['Texte compétences']} className={classes.markdownskills} />
        {domains.map(domain => (
          <div key={domain._id}>
            <h2>{lang === 'en' ? domain['Titre anglais'] : domain.Titre}</h2>
            <div>{this.getSkillsByDomain(domain._id, strapiUrl)}</div>
          </div>
        ))}
      </div>
    );
  }
}

export default Skills;
